import React, { useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import { useDispatch, useSelector } from 'react-redux';
import { togglePriceSection } from '../../actions/show_modal_Action';
import { BASE_URL } from '../../actions/api';

function PlanLimitExceed() {
    const [show, setShow] = useState(true);
    const dispatch = useDispatch();
    const { gallery_views_limit } = useSelector(state => state.authorized)
    const handleClose = () => {
        setShow(false)
        // localStorage.clear()
        // window.location.href = `${BASE_URL}/v2/users/userLoginBackAdmin`
    }
    return (
        <Modal
            show={show}
            backdrop="static"
            keyboard={false}
            size={'lg'}
            centered
        >
            <Modal.Body>
                {JSON.parse(localStorage.getItem('isAdmin')) ?
                    <button type="button" className="btn-close" onClick={handleClose}></button>
                    : <></>}
                <div className="p-lg-10 p-sm-8 mt-5 text-center">
                    <svg xmlns="http://www.w3.org/2000/svg" width="55.796" height="50.297" viewBox="0 0 55.796 50.297">
                        <g id="Group_7618" data-name="Group 7618" transform="translate(-1017.986 -423.567)">
                            <path id="Path_6215" data-name="Path 6215" d="M160.541,91.59,140.1,127a7.444,7.444,0,0,0,6.446,11.166H187.43A7.444,7.444,0,0,0,193.876,127L173.433,91.59a7.443,7.443,0,0,0-12.893,0L140.1,127a7.444,7.444,0,0,0,6.446,11.166H187.43A7.444,7.444,0,0,0,193.876,127L173.433,91.59A7.443,7.443,0,0,0,160.541,91.59Z" transform="translate(878.896 335.699)" fill="#ff4051"></path>
                            <ellipse id="Ellipse_197" data-name="Ellipse 197" cx="4.383" cy="4.383" rx="4.383" ry="4.383" transform="translate(1041.883 457.91)" fill="#fff"></ellipse>
                            <path id="Path_6217" data-name="Path 6217" d="M246.618,160.24h-1.666a2.492,2.492,0,0,1-2.492-2.492v-9.223a2.492,2.492,0,0,1,2.492-2.492h1.666a2.492,2.492,0,0,1,2.492,2.492v9.223A2.492,2.492,0,0,1,246.618,160.24Z" transform="translate(800.482 291.577)" fill="#fff"></path>
                        </g>
                    </svg>
                    <h2 className="mb-3 mt-10 h3">Your gallery is about to reach {gallery_views_limit} views</h2>
                    <p className="mb-10">Your account is close to reaching the maximum limit of gallery views as per your plan. Upgrade to our higher plans to keep flaunting engaging content on your website</p>
                    <div className="text-center">
                        <button type="button" className="btn btn-primary me-4" onClick={() => { dispatch(togglePriceSection(true, 2)); document.body.classList.add('dashUpgradeModal_opened') }}>Upgrade Now</button>
                        <a type="button" href='https://meetings.hubspot.com/prakhar-from-taggshop' target='_blank' rel='noreferrer' className="btn btn-outline-dark me-4 btn-min-w">Book a call</a>
                    </div>
                </div>
            </Modal.Body>
        </Modal>
    );
}
export default PlanLimitExceed;